export default {
	data() {
		return {
			tabs: ["全部", "已接单", "待审核","通过","失败"],
			current: 0,
			sliderOffset: 0,
			sliderLeft: 0,
		}
	},
	methods: {
		tabClick: function(e) {
			this.sliderOffset = e.currentTarget.offsetLeft;
			this.current = e.currentTarget.dataset.index;
			if(this.current == 1){
				uni.navigateTo({
					url:"../VIP-open/VIP-open"
				})
			}
		},
		toExamine(){
			uni.navigateTo({
				url:"../examine/examine"
			})
		},
	}
}
